    $shadow: 0 0 0 0 transparent;


body, html {
    font-family: $symbols_font, $main_font, monospace;
    font-size: $base_font_size * $main_font_mult;
    margin: 0;
    padding: 0;
    line-height: 1.3;
    background-color: var(--default_bg);
    color: var(--default_fg);
    //letter-spacing: -0.2px;

    ::selection, {
        //color: var(--default_bg);
       // background: var(--default_accent); 
        //background: #ffffa9;
        //color: var(--default_bg);
        //background: var(--default_accent);  
        background: #ffffa9;    
        color: var(--default_bg);
    }
    
    ::-moz-selection {
        //background: var(--default_accent);   
        background: #ffffa9;     
        color: var(--default_bg);

    }
}

// Ric Increase font size for large screens
@media (min-width: 1024px) {
    body, html {
        font-size: 20px;
        line-height: 1.5;
    }
}




body {
    min-height: 100vh;
}

p {
    //margin: (2 * $margin) 0; 
    margin: $margin 0 (2 * $margin) 0;
}


//Ric added trying to avoid brown glitch
a {
        color: var(--default_fg);
        background-color: transparent;
      }

a:hover, 
a:focus, 
a:active {
  //color: var(--default_fg);
  //background-color: var(--default_accent);

  color: var(--default_bg);
  background-color: var(--default_accent);
}




@mixin linkLike {
    color: var(--default_fg);
    /* text-decoration: none; */
    /* background-color: transparent; */
    /* border-radius: 0; */
    /* border-bottom: 1px solid currentcolor; */
    /* border-bottom: 1px solid; */

    text-decoration: underline;
    text-decoration-thickness: 1.5px;
    text-decoration-color: var(--default_accent);

    &:hover, &:focus {
        &, &:not([disabled]) {
             //border-color: var(--default_accent) !important;
             //background-color: white; 
             //background-color: var(--default_accent); 
             //text-decoration-color: var(--default_accent);
           /*  box-shadow: inset 100px #54b3d6; */
           // color: var(--dark_bg);

        }
    }
}

@mixin roundButtonLike {
    //display: inline-block;
    border-radius: $border_radius;
    background-color: var(--default_hl_bg);
    //color: var(--default_fg);
    padding: ($margin / 2);
    //margin: ($margin / 6) 0;
    border: 1px solid var(--default_dim_fg);
    //border-left: 1px solid var(--default_dim_fg);
    //border-bottom: 1px solid var(--default_dim_fg);
    //border-right: 1px solid var(--default_fg);

    &:hover, {
        background-color: var(--default_accent);
    }
}

@mixin buttonLike {
    
        @include linkLike;
    
    font-size: #{1.2 * $base_font_size};
}


a {
    @include linkLike;
}

a.unstyledLink {
    border: none;
    text-decoration: none;
    color: var(--default_fg);
}

@mixin limitWidth {
    max-width: $content_width;
    margin-left: auto;
    margin-right: auto;
}

#content {
    @include limitWidth;
    max-width: 800px;
    padding: $margin;
    
}

// Ric Increase top margin on desktop
@media (min-width: 1024px) {
    #content {
        margin-top: $margin * 3;
    }
}


/* Ric added for menu */
.navbar {
    //display: flex;
    justify-content: space-between;
    //align-items: center;

    //Fixed navbar with blurry
    display: flex;
    //justify-content: center; /* Center the whole navbar */
    align-items: center; /* Vertically align */
    position: fixed; /* Make the navbar fixed */
    top: 0; /* Stick it to the top */
    left: 0;
    width: 100%; /* Ensure it spans the entire width */
    z-index: 1000; /* Make sure it appears above other content */
    padding: 5px 0; /* Adjust padding as needed */
    
    // Blur bg
     background-color: rgba(var(--default_bg), 0.5);
     backdrop-filter: blur(10px); /* Apply the blur effect */
     -webkit-backdrop-filter: blur(3px); /* Safari support */
     background-blend-mode: overlay;

    // Text with effect
    mix-blend-mode: difference;


}

.navbar-wrapper {
    @include limitWidth; /* Apply limit width */
    display: flex;
    justify-content: space-between; /* Space between left and right sections */
    align-items: center;
    width: 100%; /* Ensure the wrapper takes up the full width of the navbar */
}

// Ric Increase font size for large screens
@media (min-width: 1024px) {
    .navbar-wrapper {
        width: 50%; 
    }
}





