}
code, table {
    padding: 2px;
}

pre code {
    border: none;
    border-radius: none;
    padding: 0
}



/* Base Table Styles */
table {
    overflow-x: auto;
    display: block;
    width: 100%;
    border-collapse: collapse;

    td, th {
        padding: 6px 12px; /* Uniform padding for alignment */
        text-align: left;
        border-bottom: 1px solid var(--default_dim_fg); 
    }

    th {
        color: var(--default_dim_fg);
        text-align: left; /* Ensure consistent alignment */
        width: 30%; /* Fixed width for headers to maintain alignment */
    }
}

/* Work History Styles */
.work-history {
    margin: 2rem 0;
}

.desktop-view {
    display: table;
}

.mobile-view {
    display: none;
}

/* Mobile View Styles */
@media (max-width: 768px) {
    .desktop-view {
        display: none;
    }

    .mobile-view {
        display: table;
        width: 100%;
        border-collapse: collapse;
        margin-bottom: 40px;
    }

    .mobile-view th, .mobile-view td {
        padding: 6px 0px; /* Same padding for all cells */
        vertical-align: top; /* Align text to the top for better visual consistency */
    }

    .mobile-view th {
        width: 40%; /* Ensure headers take up consistent space */
    }

    .mobile-view td {
        width: 60%; /* Remaining space for the data */
    }
}


  
  
















